$background-color: #e9c46a;

$primary-font-color: white;
$secondary-font-color: black;

$primary-elements-color: #2a9d8f;
$secondary-elements-color: #264653;

$primary-font: "Assistant", sans-serif;

* {
  margin: 0;
  padding: 0;
  box-sizing: 0;
}

body {
  background-color: $background-color;
  font-family: $primary-font;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  width: 100%;
}

.content-page {
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  width: 100%;
  height: fit-content;
  flex-grow: 1;
}

.page-footer {
  background-color: $secondary-elements-color;
  height: 10%;
  width: 100%;
  color: #f2f2f1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

.page-footer > p {
  margin-left: 10px;
  margin-right: 10px;
}

p > a {
  color: white;
}

.popup-window {
  position: fixed;
  left: 50%;
  bottom: 60%;
  transform: translate(-50%, 50%) scale(0);
  transition: ease-in-out 200ms;
  border: 1px solid $secondary-elements-color;
  border-radius: 10px;
  z-index: 10;
  background-color: white;
  width: 500px;
  max-width: 80%;
  max-height: 50%;
  overflow: scroll;
  overflow-y: scroll;
  opacity: 0.85;
}

.popup-window.active {
  transform: translate(-50%, 50%) scale(1);
}

.popup-window-header {
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid $secondary-elements-color;
}

.popup-window-header .popup-window-title {
  font-size: 1.25rem;
  font-weight: bold;
}

.popup-window-header .popup-window-close-button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  font-weight: bold;
  font-size: 1.25rem;
}

.popup-window-body {
  padding: 10px 15px;
}

#overlay {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: ease-in-out 200ms;
  pointer-events: none;
}

#overlay.active {
  opacity: 1;
  pointer-events: all;
}

